<script lang="ts" setup>
import { ref, h, computed, unref, onMounted, watch } from "vue";
import { NDataTable, NInput, NButton } from "naive-ui";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
const router = useRouter();
import { isPC } from "@/hooks/resize";
const pagination = {
  pageSize: 12,
};
const store = useStore();
const checkedRowKeysRef = ref([]);
const sortStatesRef = ref([]);
const firendBox = ref<null | HTMLElement>(null);
const tableMaxHeight = ref<string>("");
// offsetHeight
const initTableMaxHeight = () => {
  tableMaxHeight.value = firendBox.value.offsetHeight - 170 + "";
};

onMounted(() => {
  initTableMaxHeight();
});
const sortKeyMapOrderRef = computed(() =>
  sortStatesRef.value.reduce((result, { columnKey, order }) => {
    result[columnKey] = order;
    return result;
  }, {})
);

const handleCheck = (rowKeys) => {
  checkedRowKeysRef.value = rowKeys;
};
const createData = (len: number) => {
  const dataArr = [];
  for (let i = 0; i < len; i++) {
    dataArr.push({
      Id: i,
      Name: `name${i}`,
      Date: new Date().toLocaleString(),
      AvatarUrl: `https://${i}`,
      BlogUrl: `https://blogaaaaaaaaaaaaaaaaaaaa/${i}`,
      Motto: `Motto${i}`,
      BlogImg: `https://blogImg/${i}`,
      isEdit: false,
    });
  }
  return dataArr;
};

const data = ref(createData(200));

const headers = computed(() => [
  {
    type: "selection",
  },
  {
    title: "Id",
    key: "Id",
    align: "center",
  },
  {
    title: "Name",
    key: "Name",
    align: "center",
    ellipsis: {
      tooltip: true,
    },
  },
  {
    title: "AvatarUrl",
    key: "AvatarUrl",
    align: "center",
    ellipsis: {
      tooltip: true,
    },
  },
  {
    title: "BlogUrl",
    key: "BlogUrl",
    align: "center",
    ellipsis: {
      tooltip: true,
    },
  },
  {
    title: "Motto",
    key: "Motto",
    ellipsis: {
      tooltip: true,
    },
    align: "center",
  },
  {
    title: "BlogImg",
    key: "BlogImg",
    ellipsis: {
      tooltip: true,
    },
    align: "center",
  },
  {
    title: "Date",
    key: "Date",
    align: "center",
    ellipsis: {
      tooltip: true,
    },
  },
  {
    title: "Action",
    key: "Action",
    align: "center",
    render(row, index) {
      const pass = () => {};
      const deleteArticle = () => {
        console.log("delete");
      };
      return h(
        "div",
        {},
        {
          default() {
            return [
              h(
                NButton,
                {
                  onClick: pass,
                  type: "primary",
                  style: `margin-right:13px;margin-bottom:${
                    isPC.value ? "0" : "10px"
                  }`,
                },
                {
                  default() {
                    return "Pass";
                  },
                }
              ),
              h(
                NButton,
                { onClick: deleteArticle, type: "error" },
                {
                  default() {
                    return "Delete";
                  },
                }
              ),
            ];
          },
        }
      );
    },
  },
]);
function handleUpdateSorter(sorters) {
  console.log(sorters);
  sortStatesRef.value = [].concat(sorters);
}
const search = ref<string | null>(null);
const coypData = unref(data);
watch(search, (newVal) => {
  if (newVal == "") {
    data.value = coypData;
  }
  data.value = unref(data).filter((item) =>
    item.Name.toLocaleLowerCase().includes(newVal.trim().toLowerCase())
  );
});
</script>
<template>
  <div
    class="firendBox"
    ref="firendBox"
    :style="{
      background: store.state.theme == 'light' ? '#fff' : '#333',
    }"
  >
    <div style="margin-bottom: 20px">
      <n-button type="error" style="margin-right: 30px">DeleteAll</n-button>
      <n-input
        v-model:value="search"
        type="text"
        placeholder="Search About Name"
        clearable
        style="width: 200px"
      />
    </div>
    <n-data-table
      :columns="headers"
      :data="data"
      :pagination="pagination"
      :row-key="(row) => row.Id"
      @update:checked-row-keys="handleCheck"
      @update:sorter="handleUpdateSorter"
      striped
      :max-height="tableMaxHeight"
    >
    </n-data-table>
  </div>
</template>
<style lang="scss" scoped>
.firendBox {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 15px 10px;
  transition: background-color 0.3s var(--n-bezier);
}
</style>
